@import '../../../index';

.wrapper {
  .bg {
    position: fixed;
    inset: 0;
    @include flex-al-jc-center;
    background: rgba(86, 84, 84, 0.8);
    z-index: 5;

    .content {
      @include flex-al-center-column;
      justify-content: space-between;
      gap: 15px;
      width: 100%;
      padding: 30px;
      max-width: 500px;
      box-shadow: inset 0 0 14px 2px black;;
      border-radius: 20px;
      background: linear-gradient(180deg, rgba(103, 163, 115, 1) 6%, rgb(114, 183, 130) 38%, rgb(114, 183, 130) 65%, rgba(103, 163, 115, 1) 85%);

      & * {
        color: black;
        text-align: center;
      }

      .title {
        font-size: 2rem;
        padding: 10px 0;

        &::first-letter {
          text-transform: uppercase;
        }
      }

      .text {
        font-size: 1.4rem;

        &::first-letter {
          text-transform: uppercase;
        }

        &__span {
          font-weight: bold;
          color: #8dbef1;
          text-shadow: #0e0e0e 0 1px 1px, #0e0e0e 0 -1px 1px;
        }
      }

      .icon-wrapper {
        position: relative;
        width: 20%;
        aspect-ratio: 1/1;
        border-radius: 50%;
        background-color: white;
        overflow: hidden;

        .icon {
          width: 100%;
          height: 100%;
          object-fit: cover;
          filter: drop-shadow(4px 4px 5px black);
        }
      }
    }
  }
}
